@import '../../style/utils/mixins';
@import '../../style/utils/variables';

.nav-header {
	@include flex-end;
	text-transform: uppercase;

	@media screen and (min-width: $breakpoint-desktop) {
		text-transform: none;
	}

	&_link-home,
	&_link-about {
		@include font-right(12px);
		color: black;
		text-decoration: none;
		padding-left: 0.8rem;

		@media screen and (min-width: $breakpoint-desktop) {
			@include font-right(24px);
			padding-left: 3.5rem;
		}

		&:hover,
		&:focus
		&:active {
			text-decoration-line: underline;
		}
		
	}
}